.new-items {
  padding: 120px 45px;
  min-height: 570px;

  @media (max-width: $desktop - 1) {
    padding: 70px 45px;
    min-height: 605px;
  }

  @media (max-width: $mobile) {
    padding: 60px 0;
    min-height: 397px;
  }

  &--product-card {
    padding: 60px 0 0;

    @media (max-width: $desktop - 1) {
      padding: 45px 0 0;
    }

    @media (max-width: $mobile) {
      padding: 30px 0 0;
    }
  }

  &__container {
    max-width: 1170px;
    margin: 0 auto;

    @media (max-width: 1300px) {
      max-width: 870px;
    }

    @media (max-width: $desktop - 1) {
      max-width: 678px;
    }

    @media (max-width: $mobile) {
      min-width: 290px;
      padding: 0 15px;
    }
  }

  &__up-container {
    display: flex;
    align-items: center;

    margin-bottom: 59px;

    @media (max-width: $desktop - 1) {
      margin-bottom: 50px;
    }

    @media (max-width: $mobile) {
      margin-bottom: 40px;
    }

    a {
      font-size: 16px;
      line-height: 130%;
      color: #1d1613;

      border-bottom: 1px solid #a87b62;
      padding: 0;

      &:hover {
        color: #a87b62;
      }

      @media (max-width: $desktop - 1) {
        padding: 0 0 4px;
      }
    }
  }

  &__up-container-left,
  &__up-container-right {
    flex: 0 0 36px;
  }

  &__up-container-center {
    flex: 1 1 auto;
    text-align: center;
  }

  &__up-container--product {
    margin-bottom: 62px;

    @media (max-width: $desktop - 1) {
      margin-bottom: 48px;
    }

    @media (max-width: $mobile) {
      margin-bottom: 39px;
    }
  }

  &__up-container h2 {
    font-family: "Cormorant Garamond", "Georgia", serif;
    font-weight: bold;
    font-size: 34px;
    line-height: 130%;
    color: #1d1613;

    margin: 0 0 25px;

    @media (max-width: $desktop - 1) {
      margin: 0 0 20px;
    }
  }

  &__up-container--product h2 {
    margin: 0 0 21px;
  }
}
